.ant-layout-content {
  /* height: 100vh; */
  background: #fff;
}

.ant-menu-horizontal {
  background-color: unset;
  border-bottom: unset;
  float: right;
}
.ant-menu-horizontal > .ant-menu-item a {
  color: white;
}

.ant-menu-horizontal > .ant-menu-item-selected a {
  color: #1890ff;
}

.ant-layout-header {
  position: fixed;
  z-index: 1;
  width: 100%;
  background-color: unset;
}

.site-layout-content {
  width: 100%;
  height: 100%;
}

.site-layout-content .home-first {
  position: relative;
  width: 100%;
  height: 100vh;
}
.site-layout-content .home-first .sim {
  width: 100%;
  height: 100%;
}

.site-layout-content .home-second {
  position: relative;
  width: 100%;
  height: 100vh;
}
.site-layout-content .home-second img {
  width: 100%;
  height: 100%;
}
